import React from 'react'

export const WeatherBox = ({ weatherReport, value, n }) => {
    // const iconUrl = `http://openweathermap.org/img/wn/${weatherReport.list[value].weather[0].icon}.png`;
    function dateBuilder(d) {
        let weekDay = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
        let months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
        return `${weekDay[d.getDay()]}, ${d.getDate()} ${months[d.getMonth()]} ${d.getFullYear()}`
    }
    function getWeather() {
        const list = document.getElementById(`dropdown-list-${n}`);
        list.classList.toggle('show');
        document.getElementById(`down-arrow-${n}`).classList.toggle('rotate');
    }
    return (
        <div className="weather-box">
            <p id="dropdown-btn" onClick={e => getWeather()}>{dateBuilder(new Date(weatherReport.list[value].dt_txt))}
                <svg className="downArrow" id={`down-arrow-${n}`.toString()} width="2rem" height="2rem" viewBox="0 0 64 44" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M3 3L32 38.5L61 3" id="path" stroke="#A6B1E1" strokeWidth="6" strokeLinecap="round" />
                </svg>
            </p>
            <ul id={`dropdown-list-${n}`.toString()} className="dropdown-list">
                <hr />
                <li><span className="weather-time">{new Date(weatherReport.list[value].dt_txt).getHours()}:00</span>
                    <span className="weather-temp">{Math.round(weatherReport.list[value].main.temp)}°C</span>
                    <img src={`http://openweathermap.org/img/wn/${weatherReport.list[value].weather[0].icon}.png`} alt="" />
                    <span className="weather-desc">{weatherReport.list[value].weather[0].description}</span></li>
                <li><span className="weather-time">{new Date(weatherReport.list[value + 1].dt_txt).getHours()}:00</span>
                    <span className="weather-temp">{Math.round(weatherReport.list[value + 1].main.temp)}°C</span>
                    <img src={`http://openweathermap.org/img/wn/${weatherReport.list[value + 1].weather[0].icon}.png`} alt="" />
                    <span className="weather-desc">{weatherReport.list[value + 1].weather[0].description}</span></li>
                <li><span className="weather-time">{new Date(weatherReport.list[value + 2].dt_txt).getHours()}:00</span>
                    <span className="weather-temp">{Math.round(weatherReport.list[value + 2].main.temp)}°C</span>
                    <img src={`http://openweathermap.org/img/wn/${weatherReport.list[value + 2].weather[0].icon}.png`} alt="" />
                    <span className="weather-desc">{weatherReport.list[value + 2].weather[0].description}</span></li>
                <li><span className="weather-time">{new Date(weatherReport.list[value + 3].dt_txt).getHours()}:00</span>
                    <span className="weather-temp">{Math.round(weatherReport.list[value + 3].main.temp)}°C</span>
                    <img src={`http://openweathermap.org/img/wn/${weatherReport.list[value + 3].weather[0].icon}.png`} alt="" />
                    <span className="weather-desc">{weatherReport.list[value + 3].weather[0].description}</span></li>
                <li><span className="weather-time">{new Date(weatherReport.list[value + 4].dt_txt).getHours()}:00</span>
                    <span className="weather-temp">{Math.round(weatherReport.list[value + 4].main.temp)}°C</span>
                    <img src={`http://openweathermap.org/img/wn/${weatherReport.list[value + 4].weather[0].icon}.png`} alt="" />
                    <span className="weather-desc">{weatherReport.list[value + 4].weather[0].description}</span></li>
                <li><span className="weather-time">{new Date(weatherReport.list[value + 5].dt_txt).getHours()}:00</span>
                    <span className="weather-temp">{Math.round(weatherReport.list[value + 5].main.temp)}°C</span>
                    <img src={`http://openweathermap.org/img/wn/${weatherReport.list[value + 5].weather[0].icon}.png`} alt="" />
                    <span className="weather-desc">{weatherReport.list[value + 5].weather[0].description}</span></li>
                <li><span className="weather-time">{new Date(weatherReport.list[value + 6].dt_txt).getHours()}:00</span>
                    <span className="weather-temp">{Math.round(weatherReport.list[value + 6].main.temp)}°C</span>
                    <img src={`http://openweathermap.org/img/wn/${weatherReport.list[value + 6].weather[0].icon}.png`} alt="" />
                    <span className="weather-desc">{weatherReport.list[value + 6].weather[0].description}</span></li>
                <li><span className="weather-time">{new Date(weatherReport.list[value + 7].dt_txt).getHours()}:00</span>
                    <span className="weather-temp">{Math.round(weatherReport.list[value + 7].main.temp)}°C</span>
                    <img src={`http://openweathermap.org/img/wn/${weatherReport.list[value + 7].weather[0].icon}.png`} alt="" />
                    <span className="weather-desc">{weatherReport.list[value + 7].weather[0].description}</span></li>
                <li><span className="weather-time">{new Date(weatherReport.list[value + 8].dt_txt).getHours()}:00</span>
                    <span className="weather-temp">{Math.round(weatherReport.list[value + 8].main.temp)}°C</span>
                    <img src={`http://openweathermap.org/img/wn/${weatherReport.list[value + 8].weather[0].icon}.png`} alt="" />
                    <span className="weather-desc">{weatherReport.list[value + 8].weather[0].description}</span></li>
            </ul>
        </div>
    )
}
